<template lang="html">
  <div id="container">
		<div id="content" ref="mapArea">
			<div class="wrap_area">
				<a href="#" class="btn_present"><img src="../assets/images/btn_position.png" alt="현재위치" /></a>
			</div>			
		</div>
	</div>
</template>

<script lang="js">
  export default  {
    name: 'geo-map',
    props: [],
		computed: {},
		mounted() {
			this.initMap();
    },
    data() {
      return {
				map:null
      }
		},
		created() {
			_eventBus.$on('mapCover', this.mapCoverHandler);
		},
		methods: {
			initMap:function(){
				//this.$refs.mapArea.style.height = window.innerHeight/2 + "px";
				this.map = new olleh.maps.Map(this.$refs.mapArea.id, {
					zoomControl: false,
					zoomControlOptions: {
						top: 10,
						right: 15
					},
					panControl: false,
					scaleControl: true,
					scaleControlOptions:{
							left: 7, // 기본 14
							bottom:5
					},
					copyrightControl: true,
					copyrightControlOptions:{bottom:9},
					mapTypeControl: false,
					measureControl: false,
					measureControlOptions:{right:15}
				});

				var coordCenter = new olleh.maps.UTMK(953828.1032726372, 1952440.2230947923); //958386.063532902, 1941447.5761742294:우면동, 953828.1032726372, 1952440.2230947923:광화문, 965898.9999999991, 1928928.9999999197:분당
				this.map.setCenter(coordCenter);
				this.map.setZoom(11);
				window._map = this.map;
			},

			mapCoverHandler: function(status){
				if(status.wOpenStatus){
					this.$refs.mapArea.style.height = window.innerHeight/2 + "px";
				}else{
					if(status.resultAreaVisible){
						this.$refs.mapArea.style.height = (window.innerHeight-99) + "px";
					}else{
						this.$refs.mapArea.style.height = window.innerHeight + "px";
					}
				}
				window.dispatchEvent(new Event('resize'));
			}
		}
	}
</script>

<style scoped>
</style>



