<template lang="html">
  <div>
    <geo-map></geo-map>
    <poi-search :visible="poiSearchVisible" @moveRoute="moveRouteHandler"></poi-search>
    <route-search :visible="routeSearchVisible" @movePoi="movePoiHandler"></route-search>
  </div>
</template>

<script lang="js">
  import PoiSearch from '@/components/PoiSearch'
  import RouteSearch from '@/components/RouteSearch'
  import GeoMap from '@/components/GeoMap'
  export default  {
    name: 'root',
    props: [],
    components:{PoiSearch, RouteSearch, GeoMap},
    
    mounted() {},

    data() {
      return {
        poiSearchVisible: true,
        routeSearchVisible: false
      }
    },

    methods: {
      moveRouteHandler: function(){
        this.poiSearchVisible = false;
        this.routeSearchVisible = true;
      },

      movePoiHandler: function(){
        this.poiSearchVisible = true;
        this.routeSearchVisible = false;
      }
    },

    computed: {}
}
</script>

<style scoped>
</style>


