<template lang="html">
<div class="geo_box" v-show="visible"> 
  <div class="wrap_Gsearch02" id="geo_box02">
    <div>
      <button class="btn_prev" @click="movePoiClick">길찾기 화면 변경</button>
      <span class="seach_inp">
        <input type="text" title="출발지가 어디신가요?" placeholder="출발지가 어디신가요?" class="input_act02" />
      </span>
      <button class="btn_gclose"  id="searchBox">삭제</button>
    </div>
    <div class="wrap_inpAdd">
      <button class="btn_exchange">출,도착 변경</button>
      <span class="seach_inp">
        <input type="text" title="도착지가 어디신가요?" placeholder="도착지가 어디신가요?" class="input_act03" />
      </span>
      <button class="btn_gclose"  id="searchBox">삭제</button>
    </div>
  </div>
</div>
</template>

<script lang="js">
  export default  {
    name: 'route-search',
    props: {
      visible:{type:Boolean, default:false}
    },
    mounted() {},

    data() {
      return {
        resultAreaVisible: false,
        wOpenStatus: false
      }
    },

    watch: {
      visible: function(val){
        if(val){
          _eventBus.$emit('mapCover', {wOpenStatus:this.wOpenStatus, resultAreaVisible:this.resultAreaVisible});
        }
      },
    },

    methods: {
      movePoiClick: function(){
        this.$emit('movePoi')
      }
    },
    computed: {

    }
}
</script>

<style scoped>
  .routes-search {

  }
</style>


